<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>展示收集的意见反馈信息</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>意见类型</label>
        <el-select v-model="queryParams.proposalDictType" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option label="功能异常" :value="1"></el-option>
          <el-option label="体验问题" :value="2"></el-option>
          <el-option label="新功能建议" :value="3"></el-option>
          <el-option label="其他" :value="4"></el-option>
        </el-select>
      </div>
      <div class="query-group">
      <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="proposalDictType" label="意见类型" min-width='160'>
        <template slot-scope="scope">
          <span v-if="scope.row.proposalDictType==='1'">功能异常</span>
          <span v-if="scope.row.proposalDictType==='2'">体验问题</span>
          <span v-if="scope.row.proposalDictType==='3'">新功能建议</span>
          <span v-if="scope.row.proposalDictType==='4'">其他</span>
        </template>
      </el-table-column>
      <el-table-column prop="description" label="意见描述" min-width="240">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.description">{{scope.row.description}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="contactName" label="联系人姓名" min-width="160">
        <template slot-scope="scope">
         <div class="text-overflow" :title="scope.row.contactName">{{scope.row.contactName}}</div>
         </template>
      </el-table-column>
      <el-table-column prop="contactPhone" label="联系号码" min-width="160"></el-table-column>
      <el-table-column label="操作选项" width='260' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button  icon='edit' size='small' type='info' @click.stop="showDialog(scope.row.id)">查看</cms-button>
        </div>
      </el-table-column>


    </el-table>
      <!-- 弹出框 -->
      <el-dialog title="详情" :visible.sync="interestsDialog" width="530px">
        <el-form style="padding-right:20px;" label-width="140px">
          <el-form-item label="意见类型:">
             <span v-if="getFormData.proposalDictType==='1'">功能异常</span>
             <span v-if="getFormData.proposalDictType==='2'">体验问题</span>
             <span v-if="getFormData.proposalDictType==='3'">新功能建议</span>
             <span v-if="getFormData.proposalDictType==='4'">其他</span>
          </el-form-item>
          <el-form-item label="意见描述:">
            <span style="word-break: break-all;">{{getFormData.description}}</span>
                 <!-- <span style="word-break: break-all;">{{showFirst(getFormData.description)}}</span>
                 <transition name="el-collapse-transition">
                   <span v-show="getFormData.showDesc">{{showLeft(getFormData.description)}}</span>
                  </transition>
                 <span class="show-more" v-if="getFormData.description.length>100&&!getFormData.showDesc"
                  @click="toggleShow()">
                  查看更多
                 </span>
                  <span class="show-more" v-if="getFormData.showDesc"
                  @click="toggleShow()">
                  隐藏更多
                 </span> -->
          </el-form-item>
          <el-form-item label="附图:">
          <cms-picture-zone :data='getFormData.spaceDatas' :disabled='true'></cms-picture-zone>
          </el-form-item>
          <el-form-item label="联系姓名:">
            <span style="word-break: break-all;">{{getFormData.contactName}}</span>
          </el-form-item>
          <el-form-item label="联系号码:">
            <span>{{getFormData.contactPhone}}</span>
          </el-form-item>
        </el-form>
      </el-dialog>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  proposalPage, proposalGet
} from '@/api/marketing/index.js'
var defaultData = {
  proposalDictType: '',
  page: 1,
  size: 10
}
var formData = {
  contactName: '',
  contactPhone: '',
  description: '',
  id: '',
  spaceDatas: []
}
export default {
  mixins: [list],
  data() {
    return {
      interestsDialog: false,
      queryParams: Object.assign({}, defaultData),
      getFormData: Object.assign({}, formData)
    }
  },
  methods: {
    // showFirst(target) { // 显示首次评论信息的长度
    //   return target.substr(0, 100)
    // },
    // showLeft(target) { // 显示剩余评论信息的长度
    //   return target.substring(100)
    // },
    // toggleShow() {
    //   this.$set(this.getFormData, 'showDesc', !this.getFormData.showDesc)
    // },
    showDialog(id) {
      this.interestsDialog = true
      proposalGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.getFormData = Object.assign(formData, res.data)
          this.getFormData.showDesc = false
        } else {
          this.$errorMessage('获取失败')
        }
      })
    }
  },
  created() {
    // 首次触发
    this.getTableData(proposalPage, this.queryParams)
  }
}
</script>

<style>
</style>



